.RoomView {
  .Separator {
    height: 60vh;
    width: 1px;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
    @media only screen and (max-width: 37.5em) { 
      height: 1px;
      width: 70vw;
      background-image: linear-gradient(to left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
    }
  }
  .RightSection {
    height: 100%;
    min-width: 40vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 35vh;
    padding-bottom: 2rem;
    
    @media only screen and (max-width: 37.5em) { 
      padding-top: 0;
    }
  }
}